::-webkit-scrollbar {
    width: 0px;
    background: transparent; /* make scrollbar transparent */
}
a:hover{
    color: #008081 !important;
    /* font-size: 17px; */
}
.rotate{
    transform:rotate(270deg);
    height:110px;
    width:123px;
    font-size: 20px;
    /* top:50%; */
    position:fixed;
    margin:80px auto;
}
#secondScr
{
    padding-top: 55px;
}
@media only screen and (max-width: 768px) {
    #header{
        /* height:45px; */
        width: 92%;
        top:92px;
    }
    #top{
        
        margin-top: 100px
    }
    #space{
        
        width: auto;
        width: 100vw;
        top:88px
    }
    
}
@media only screen and (min-width: 768px) {
    #header{
        min-width:518px;
        max-width:518px;
        top:52px;
    }
    #top{
        margin-top: 62px;
    }
    #secondScr{
        min-width:550px;
        max-width:550px; 
    }
    #space{
        min-width:550px;
        max-width:550px;
        top:40px
    }
}